.wrapper{
  margin-top: 50px;
  height: 450px ;
  padding: 0 15px;
  display:grid;
  align-items:center;
  border-bottom: 1px solid #222;
}
.MainTitleTxt1{
  font-size: 28px;
  margin: 0;
}
.MainTitleTxt2{
  font-size: 28px;
  margin: 0;
  font-weight: normal;
  margin-top: 15px;
  margin-bottom: 75px;
}
.MainSubTxt{
  font-size: 12px;
  margin: 0;
  margin-top: 18px;
}
.LeftTxt{
  display: none;
}
.RightTxt{
  display: none;
}
.BottomTxt{
  display: none;
}

@media screen and (min-width: 1200px) {
  .wrapper{
    position: relative;
    height:100vh ;

  }
  .TxtBox{
    display: flex;
    padding: 0 140px;
    justify-content: center;
    align-items: center;
  }
  .MainTitleTxt1{
    font-size: 80px;
    margin: 0;
  }
  .MainTxt{
    margin-right: 10vw;
  }
  .MainTitleTxt2{
    font-size: 80px;
    margin: 0;
    font-weight: normal;
    margin-top: 15px;
    margin-bottom: 75px;
  }
  .MainSubTxt{
    font-size: 24px;
    margin: 0;
    margin-top: 18px;
  }
  .LeftTxt{
    display:block;
    position: absolute;
    font-size: 14px;
    top: 50;
    left: -10px;
    transform: rotate(-90deg);
    margin: 0;
  }
  .RightTxt{
    display:block;
    position: absolute;
    font-size: 14px;
    top: 50;
    right: -30px;
    transform: rotate(-90deg);
    margin: 0;
  }
  .BottomTxt{
    display:block;
    position: absolute;
    font-size: 14px;
    bottom: 150px;
    left: 100px;
    margin: 0;
  }
}