.ProjectMainTxt{
  padding: 0 15px;
  height: 340px;
  border-bottom: 1px solid #222;
  display:grid;
  align-items:center;
}
.ProjectMainTxt h1{
  font-size: 28px;
  margin: 0;
}
.ProjectMainTxt p{
  font-size: 12px;  
  margin: 0;
  margin-top: 35px;
}
.SwiperBox{
  border-bottom: 1px solid #222;
}
.projectSwiper {
  width: 100%;
  height: 40vw;
  margin: 20px auto;
}

.projectSwiperSlide {
  width: 115px;
  text-align: center;
  font-size: 18px;
  background: #ddd;

  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-shrink: 1; */
}
.SubTxtBox{
  display: none;
}
@media screen and (min-width: 1200px) {
  .ProjectMainTxt{
    padding: 0 65px;
    height: 430px;
  }
  .ProjectMainTxt h1{
    font-size: 64px;
    margin: 0;
    margin-right: 270px;
  }
  .ProjectMainTxt p{
    font-size: 24px;  
  }
  .MainTxtBox{
    display: flex;
  }
  .SwiperBox{
    display: flex;
  }
  .SwiperTxtBox{
    position: relative;
    border-right: 1px solid #222;

    height: 770px;
    width: 480px;

  }
  .SwiperTitle{
    width: 480px;
    margin: 0;
    font-size: 28px;
    padding-left: 65px;
    padding-top: 45px;
  }
  .SubTxtBox{
    display: block;
    position: absolute;
    bottom: 45px;
    left: 65px;
  }
  .SubTxtBox h3{
    font-size: 14px;
    margin: 0;
    width: 80px;
  }
  .SubTxtBox p{
    font-size: 14px;
    margin: 0;
  }
  .SubTxt1{
    display: flex;
  }
  .SubTxt2{
    margin-top: 25px;
    display: flex;
  }
  .SubTxt3{
    margin-top: 25px;
    display: flex;
  }
  .SubTxt4{
    margin-top: 50px;
  }
  .SubTxt4 ul{
    display: flex;
  }
  .SubTxt4 li{
    list-style: none;
    margin-right: 5px;
    background-color: #ddd;
    padding: 2px 10px;
    border-radius: 13px;
    margin-top: 15px;
  }

/* 스와이퍼 */

  .projectSwiper {
    height: 690px;
    margin: 40px auto;
  }
  
  .projectSwiperSlide {
    width: 460px;
  }
}