/* 스와이퍼 */
.mySwiper {
  min-width: 390px;
  height: 35vw;
  margin: 20px ;

}

.mySwipeSlide {
  text-align: center;
  font-size: 18px;
  background: transparent;
  /* flex-shrink: 1; */
  /* width: 230px; */
}

.ImgBox{
  background-color: #ddd;
  width: 30vw;
  height: 20vw;
  margin-right: 15px;
}
.title{
  margin: 0;
  font-size: 14px;
  margin-top: 18px;
  text-align: left;
}
.skills{
  margin: 0;
  font-size: 10px;
  margin-top: 15px;
  text-align: left;
  font-weight: normal;
}
.period{
  margin: 0;
  font-size: 8px;
  margin-top: 18px;
  text-align: left;
  font-weight: bold;
}
.date{
  margin: 0;
  font-size: 8px;
  margin-top: 10px;
  text-align: left;
}
/* 페이지 */
.wrapper{
  border-bottom: 1px solid #222;

}
.ToyProjectMainTxt{
  padding: 0 15px;
  height: 340px;

  display:grid;
  align-items:center;
}
.ToyProjectMainTxt h1{
  font-size: 28px;
  margin: 0;
}
.ToyProjectMainTxt p{
  font-size: 12px;  
  margin: 0;
  margin-top: 35px;
}

@media screen and (min-width: 1200px) {
  .ToyProjectMainTxt{
    padding: 0 65px;
    height: 430px;
  }
  .ToyProjectMainTxt h1{
    font-size: 64px;
    margin: 0;
    margin-right: 270px;
  }
  .ToyProjectMainTxt p{
    font-size: 24px;  
  }
  .MainTxtBox{
    display: flex;
  }
  .mySwiper {
    height: 25vw;
  }
  .ImgBox{
    width: 30vw;
    height: 20vw;
    margin-right: 15px;
  }
  .period{
    display: none;
  }
  .date{
    display: none;
  }
}