@media screen and (min-width: 1200px) {
  .side {
    height: 100px;
  }
  .title{
    font-size: 32px;
    margin-left: 65px;
  }
  .headerBtn {
    top: 40px;
    right: 65px;
  }
  .headerBtn svg{
    width: 25px;
    height: 25px;
  }
}