
.SkillsMainTxt{
  font-size: 28px;
  margin: 0;
  border-bottom: 1px solid #222;
  padding: 15px;
}

/* skill item */
.skillItem{
  padding: 0 15px 30px;
  margin-top: 30px;
  border-bottom: 1px solid #222;
}
.skillItem h2{
  margin: 0;
  font-size: 20px;
}

.skillItem p{
  margin: 0;
  margin-top: 50px;
  font-size: 12px;
}
.skillItem ul{
  margin-top: 25px;
  padding-left: 15px;
}
.skillItem li{
  font-size: 10px;
  margin-top: 18px;
}

@media screen and (min-width: 1200px) {
  .SkillsMainTxt{
    font-size: 64px;
    margin: 0;
    border-bottom: 1px solid #222;
    padding: 50px 65px;
  }
  .skillItem{
    display: flex;
    margin: 0;
    padding: 0;
  }
  .skillmain{
    width: 485px;
    border-right: 1px solid #222;
  }
  .skillItem h2{
    padding: 50px 65px;
  }
  .skillItem p{
    margin: 0;
    margin-top: 55px;
    padding-left: 70px;
    font-size: 18px;
  }
  .skillItem ul{
    margin-top: 55px;
    margin-bottom: 55px;
    padding-left: 85px;
  }
  .skillItem li{
    font-size: 18px;
    margin-top: 20px;
  }
}