
.MainTitle{
  font-size: 28px;
  margin: 100px 0 0;
  border-bottom: 1px solid #222;
  padding-bottom: 80px;
  padding-left: 15px;
}
.linkList{
  display: flex;
  flex-direction: column;
  width: 100%;
}
.linkList a{
  border-bottom: 1px solid #222;
  text-decoration: none;
  color: #000;
  padding-left: 15px;
  padding-bottom: 12px;
  margin-top: 58px;
  font-size: 12px;
}

@media screen and (min-width: 1200px) {
  .wrapper{
    display: flex;
  }
  .MainTitle{
    font-size: 64px;
    border-right: 1px solid #222;
    padding-left: 65px;
    padding: 140px 115px 140px 65px;
    margin: 0;
  }
  .linkList{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
  }
  .linkList a{
    border-right: 1px solid #222;
    text-decoration: none;
    color: #000;
    padding: 0;
    margin:0;
    font-size: 24px;
    width: 100%;
    padding-top: 310px;
    padding-left: 25px;
  }
  .linkList svg{
   margin-top: 10px; 
  }
}